import moment from "moment";
import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { MediaBaseUrl } from "../../../../actions/api";
import TopcardSkeleton from "../../analytics-new/layout/skeleton/topcardSkeleton";
import { PopupButton } from "react-calendly";
import { kFormatter } from "../../../../utils";
import PercentDifference from "../../analytics-new/layout/common/percentDifference";
import { togglePlanSection_D } from "../../../../actions/display/showDisplayModalAction";
import { useDispatch } from "react-redux";
import TopFilter from "./common/topFilter";

    const BookCall = (props) => {

    const hour = moment().hour();
    const dispatch = useDispatch()

    const { allfeedsCount, alltotalPostcount, numberOfActiveWalls, userData, totalWallByRules, totalFeedsByRules, loader, activeScreen, alltotalPostdifference, alltotalPostflag } = props

    const totalData = [{ 'name' : 'Total Walls', 'icon' : 'home-walls', 'total'  : totalWallByRules, 'count' : numberOfActiveWalls, 'tooltip' : 'Number of walls capturing the attention of your attendees','border' : '0' },
                       { 'name' : 'Total Feeds', 'icon' : 'home-feeds', 'total' : totalFeedsByRules,  'count' : allfeedsCount, 'tooltip' : 'Total feeds created using social media. Each feed represents one network','border' : '2' },
                       { 'name' : 'Total Posts', 'icon' : 'home-post', 'difference' : alltotalPostdifference , 'count' : alltotalPostcount, 'tooltip' : 'Total number of eye-catching posts displayed on your Social Walls','border' : '1' },
                       { 'name' : 'Active Screens', 'icon' : 'active-screen', 'count' : activeScreen, 'tooltip' : 'Know how many screens are showing your walls','border' : '3' }]

    return (
        <>
            <div className="customer-support-main rounded-8x mb-5">
                <TopFilter {...props}/>
                <div className="row align-items-center">
                    
                    <div className="col-md-6">
                    <div className="customer-info">   
                        <h4 className="font-weight-boldest mb-4 text-capitalize"> Hey, {userData.user.name ? userData.user.name.split(' ')[0] : ''} 👋</h4>
                        <p className="font-size-h6"> Looking for Something? </p>
                        <div className="mt-6">
                        <button onClick={window.Intercom ? (e) => { window.Intercom('show') } : () => { }}  className="btn btn-outline-dark me-3 btn-min-w">
                            <span className="position-relative">Chat Now</span>
                        </button>
                      
                        <PopupButton
                            url="https://calendly.com/taggbox/demo"
                            rootElement={document.getElementById("dshsboard-page")}
                            text="Book A Call"
                            className="btn btn-primary btn-min-w"
                        />
                        </div>
                    </div>
                    </div>
                    <div className="col-md-6">
                    <div className="customer-success-manager rounded-8x">
                        <div className="d-flex success-manager-info rounded-8x">
                        <div className="customer-success-details col-8 py-5 px-9 ">
                            <p className="text-secondary text-uppercase font-size-md">
                            Raj from social walls
                            </p>
                            <p className="text-dark font-size-lg">
                                 Ready to get maximum engagement at your next event? Try Social Walls today and boost your social media activity by 2x.
                            </p>
                            <div
                            href="#" onClick={()=>dispatch(togglePlanSection_D(true))} 
                            className="text-decoration-underline text-dark font-size-h6 font-weight-boldest cursor-pointer"
                            >
                            Get Your Social Wall
                            </div>
                        </div>
                        <div className="customer-success-image position-relative col-4">
                            <img
                            className="tb_cp_image"
                            role="img"
                            src="https://cloud.socialwalls.com/react-app/media/images/customer-success@2x.png?v=1.0.0"
                            height="233"
                            width="239"
                            />
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
            <div className="row">

            { loader ? <TopcardSkeleton res={3} col={4} /> : totalData.map((e)=>{
                return(<div className="col-sm-6 col-lg-3 mb-5">
                    <div className={`card-border-top top-border-${e.border} p-5 card-shadow card`}>
                        <div className="d-flex align-items-center">
                            <div className="icon-box d-flex align-items-center justify-content-center rounded-8x me-3">
                                <img src={`${MediaBaseUrl}icons/svg-icons/${e.icon}.svg`} width={"28px"} height={"28px"}/>
                            </div>
                            <div className="">
                                <span className="font-weight-bold font-size-h5 mb-2 text-secondary d-block"> {e.name}
                                    <OverlayTrigger
                                        placement="top"
                                        overlay={  <Tooltip>{e.tooltip}</Tooltip>}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" className="ms-1"><g id="Group_5749" data-name="Group 5749" transform="translate(12215 -4805)"><rect id="Rectangle_2065" data-name="Rectangle 2065" width="24" height="24" transform="translate(-12215 4805)" fill="none" opacity="0"></rect><path id="Path_6691" data-name="Path 6691" d="M20,11a9,9,0,1,0,9,9A9,9,0,0,0,20,11Zm.923,12.692a.923.923,0,0,1-1.846,0V19.308a.923.923,0,1,1,1.846,0ZM20,17.231a.923.923,0,1,1,.923-.923A.923.923,0,0,1,20,17.231Z" transform="translate(-12223 4797)" fill="#959595" opacity="0.999"></path></g></svg>
                                   </OverlayTrigger>     
                                </span>
                                <h4 className="font-size-h2 mb-0 d-flex align-items-center"> {kFormatter(e.count)}{e.total ? `/${e.total}` : null} 

                                 { e.difference ?  
                                      <PercentDifference difference={e.difference} flag={alltotalPostflag} /> : null }  
                                </h4>
                            </div>
                        </div>
                    </div>
                </div>)
               })}

             </div>
        </>
    );
 };

 export default React.memo(BookCall)