    import moment from "moment";
    import React from "react";
    import DateRangePicker from "react-bootstrap-daterangepicker";

    const TopFilter = (props) => {
    const { startDate, endDate, filterDashbord } = props;

    return (
        <div className="bookCall-datepicker cursor-pointer">
        <DateRangePicker
            startDate={startDate}
            endDate={endDate}
            initialSettings={{
            locale: { format: "DD-MM-YYYY", separator: "-" },
            maxDate: moment().endOf("month").format("DD-MM-YYYY"),
            showDropdowns: true,
            startDate: startDate,
            endDate: endDate,
            ranges: {
                Today: [
                moment().format("DD-MM-YYYY"),
                moment().format("DD-MM-YYYY"),
                ],
                Yesterday: [
                moment().subtract(1, "days").format("DD-MM-YYYY"),
                moment().subtract(1, "days").format("DD-MM-YYYY"),
                ],
                "Last 7 Days": [
                moment().subtract(6, "days").format("DD-MM-YYYY"),
                moment().format("DD-MM-YYYY"),
                ],
                "Last 30 Days": [
                moment().subtract(30, "days").format("DD-MM-YYYY"),
                moment().format("DD-MM-YYYY"),
                ],
                "Last Month": [
                moment()
                    .subtract(1, "month")
                    .startOf("month")
                    .format("DD-MM-YYYY"),
                moment().subtract(1, "month").endOf("month").format("DD-MM-YYYY"),
                ],
            },
            }}
            onCallback={filterDashbord}
        >
            <div className="d-flex align-items-center dashboard_dataRange dateRange">
            <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="currentColor"
                height={18}
            >
                <path d="M9 1V3H15V1H17V3H21C21.5523 3 22 3.44772 22 4V20C22 20.5523 21.5523 21 21 21H3C2.44772 21 2 20.5523 2 20V4C2 3.44772 2.44772 3 3 3H7V1H9ZM20 11H4V19H20V11ZM11 13V17H6V13H11ZM7 5H4V9H20V5H17V7H15V5H9V7H7V5Z"></path>
            </svg>
            <div className="d-flex align-items-center db_dateRange ms-2">
                <div className="db_date">
                {moment(startDate, "DD-MM-YYYY").format("ll")}
                </div>
                <span>-</span>
                <div className="db_date">
                {moment(endDate, "DD-MM-YYYY").format("ll")}{" "}
                </div>
                <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                height={18}
                fill="currentColor"
                >
                <path d="M11.9999 13.1714L16.9497 8.22168L18.3639 9.63589L11.9999 15.9999L5.63599 9.63589L7.0502 8.22168L11.9999 13.1714Z"></path>
                </svg>
            </div>
            </div>
        </DateRangePicker>
        </div>
    );
    };

    export default React.memo(TopFilter);
