import React from "react";

const Card = (e) => {
  return (
    <div class={`col-sm-6 col-md-6 col-xxl-${e.col} mb-5`}>
      <div class="card card-custom card-stretch home-widget-section p-6">
        <div class="sk_loader_">
          <div class="wall_progress">
            <p class="sk_line sk_line_height_10 sk_line_80 mb-7"></p>
            <h3 class="sk_line sk_line_40 sk_line_height_26 mb-5"></h3>
          </div>
        </div>
      </div>
    </div>
  );
};

const TopcardSkeleton = (props) => {
  const { col, res } = props;
  let cols = Array.from({ length: col }, (_, index) => index);

  return (
    cols &&
    cols.map(() => {
      return <Card col={res} />;
    })
  );
};

export default React.memo(TopcardSkeleton);
